import React, { useEffect, useState } from "react";
import SVG from "react-inlinesvg";
import {toAbsoluteUrl} from "../../../_metronic/_helpers";
import { FormattedMessage} from "react-intl";
import { useHistory } from "react-router-dom";

import AliceCarousel from 'react-alice-carousel';
import "react-alice-carousel/lib/alice-carousel.css";

import MenuItem from '@material-ui/core/MenuItem';
import TextField from '@material-ui/core/TextField';
import { makeStyles } from '@material-ui/core/styles';

import templates from "../Component/templates.json";
import Button from '@material-ui/core/Button';

const initialValues = {
  random: "1",
  section: "1",
  brand: "1",

};

export function Template() {

	const history = useHistory();

	function goBack() {
      history.goBack();
    }

    const useStyles = makeStyles(theme => ({
		container: {
		    display: 'flex',
		    flexWrap: 'wrap',
		},
		textField: {
		    marginLeft: theme.spacing(1),
		    marginRight: theme.spacing(1),
		},
		dense: {
		    marginTop: theme.spacing(2),
		},
		menu: {
		    width: 200,
		},
	}));

    const classes = useStyles();
    const [values, setValues] = React.useState({
	    random: '1',
	    section: '1',
	    brand: '1',
	});
    const [state, setState] = React.useState({
	      items: [],
	      visible: 9,
	      error: false,
	      count:0
	});

	const handleChange = name => event => {
    	setValues({ ...values, [name]: event.target.value });
    	setState({ ...state, [name]: event.target.value, });
	};

    const random = [
      {
        value: '1',
        label: 'RANDOM',
      },
      {
        value: '2',
        label: 'NEW',
      },
      {
        value: '3',
        label: 'MOST POPULAR',
      },
      {
        value: '4',
        label: 'RECOMMENDED',
      }
  	];

    const section = [
      {
        value: '1',
        label: 'ALL',
      },
      {
        value: '2',
        label: 'NAVIGATION',
      },
      {
        value: '3',
        label: 'HEADER',
      },
      {
        value: '4',
        label: 'INTRO',
      },
      {
        value: '5',
        label: '1 COLUMN',
      },
      {
        value: '6',
        label: '2 COLUMN',
      },
      {
        value: '7',
        label: '3 COLUMN',
      },
      {
        value: '8',
        label: 'BANNER',
      },
      {
        value: '9',
        label: 'OUTER',
      },
      {
        value: '10',
        label: 'FOOTER',
      },
      {
        value: '11',
        label: 'UNSUBSCRIBE',
      }
  	];

    const brand = [
      {
        value: '1',
        label: 'ALL',
      },
      {
        value: '2',
        label: 'Architectural',
      },
      {
        value: '3',
        label: 'Art',
      },
      {
        value: '4',
        label: 'Baby',
      },
      {
        value: '5',
        label: 'Beauty',
      },
      {
        value: '6',
        label: 'Business',
      },
      {
        value: '7',
        label: 'Cars & Bikes',
      },
      {
        value: '8',
        label: 'Clothes',
      },
      {
        value: '9',
        label: 'Creative',
      },
      {
        value: '10',
        label: 'Development',
      },
      {
        value: '11',
        label: 'Education',
      },
      {
        value: '12',
        label: 'Entertainment',
      }
  	];

  	useEffect(() => {
  		setState({items: templates, visible: 9})
	  }, []);


  	function loadMore() {
	  	setState((prevState) => ({
	  		...prevState,
		  	visible: prevState.visible + 9
		}));
	 }


	const  viewTeplate = (id, e) => {
	    e.preventDefault();
	    history.push({
		  	pathname: '/campaigns/template/view/'+id,
		  	state: {
		  		id: id,
		  	}
		});
		console.log(id);
	}

	return (<>
		<div className="row" id="modules-filter">	
			<div className="col-md-12 slides-section">
				<div className="row">
					<div className="col-md-8">
						<AliceCarousel autoPlay={true} autoPlayInterval={3000} autoPlayDirection="ltr">
						    <img src="/media/template/banner1.png" className="sliderimg" alt=""/>
						    <img src="/media/template/banner2.png" className="sliderimg" alt=""/>
						    <img src="/media/template/banner3.png" className="sliderimg" alt=""/>
						</AliceCarousel>
					</div>
					<div className="col-md-4">
						<div id="store-header-sections">
							<ul className="header-sections clear-fix">
								<li data-section="1" data-dropdown-type-value="Development">
									<div className="section">
										<div className="section-caption">
											<h4 className="font-archivo">Promotions</h4>
											<h6 className="font-bold">261 modules</h6>
										</div>
									</div>
								</li>
								<li data-section="2" data-dropdown-type-value="Creative">
									<div className="section">
										<div className="section-caption">
											<h4 className="font-archivo">Creative</h4>
											<h6 className="font-bold">269 modules</h6>
										</div>
									</div>
								</li>
								<li data-section="3" data-dropdown-type-value="Business">
									<div className="section">
										<div className="section-caption">
											<h4 className="font-archivo">Business</h4>
											<h6 className="font-bold">391 modules</h6>
										</div>
									</div>
								</li>
								<li data-section="4" data-dropdown-type-value="Most Populair">
									<div className="section">
										<div className="section-caption">
											<h4 className="font-archivo">Most Popular</h4>
											<h6 className="font-bold">Discover them now</h6>
										</div>
									</div>
								</li>
							</ul>
						</div>
					</div>
				</div>
			</div>
			
			<div className="col-md-12 all-template-filter-Blk">
				<TextField
                    select
                    name="random"
                    variant="outlined"
                    className={classes.textField}
                    value={values.random}
  					onChange={handleChange('random')}
                >
                    {random.map(option => (
                      <MenuItem key={option.value} value={option.value}>
                        {option.label}
                      </MenuItem>
                    ))}
                </TextField>

                <TextField
                    select
                    name="section"
                    variant="outlined"
                    className={classes.textField}
                    value={values.section}
  					onChange={handleChange('section')}
                >
                    {section.map(option => (
                      <MenuItem key={option.value} value={option.value}>
                        {option.label}
                      </MenuItem>
                    ))}
                </TextField>

                <TextField
                    select
                    name="brand"
                    variant="outlined"
                    className={classes.textField}
                    value={values.brand}
  					onChange={handleChange('brand')}
                >
                    {brand.map(option => (
                      <MenuItem key={option.value} value={option.value}>
                        {option.label}
                      </MenuItem>
                    ))}
                </TextField>
			</div>

			<div className="col-md-12">
	      		<section className="templateFeed feeds">
			        <div className="tiles" aria-live="polite">
			          {state.items.slice(0, state.visible).map((item, index) => {
			              return (
			                <div className="tile fade-in" key={item.id} value={item.id}>
			                  <div className="imageBlk" value={index+1}  onClick={(e) => viewTeplate(item.id, e)}><img src={`/media/template/`+item.image} height="auto" width="100%" /></div>
			                  <div className="feedBase"><span className="tempTitle">{item.title}</span> <Button variant="contained" className="add-template"><i className="flaticon2-plus icon-1x"></i> Add Template</Button></div>
			                </div>
			              );
			            })}
			        </div>
			        {state.visible < state.items.length &&
			              <Button variant="contained" className='load-more' onClick={loadMore}> Load more </Button>
			          }
		        </section>
	      	</div>

		</div>
	</>);

}